.brag {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form {
    text-align: center;
}

.form > div {
    text-align: left;
    margin-bottom: 10px;
}

.form > div > span {
    text-align: right;
    display: inline-block;
    min-width: 100px;
    margin-right: 20px;
}

.form > button:last-child {
    margin-left: 10px;
}

.table td, .table th {
    height: 25px;
    min-width: 60px;
    padding: 5px;
    text-align: center;
    border: 1px solid #888;
};

